import React, { Component } from 'react';
import JSONSchemaForm from 'react-jsonschema-form';

class App extends Component {
  constructor() {
    super();
    this.state = { 
      schema: {}, 
      justSaved: false 
    };
  }

  async componentDidMount() {
    const result = await fetch('http://localhost:3000/projects/schema');  
    const schema = await result.json();

    this.setState((state, props) => ({ schema }))
  }

  async submit (event) {
    console.log(event)
    await fetch('http://localhost:3000/projects/report', { 
        method: 'POST', 
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ 
          schema: this.state.schema, 
          data: event.formData
        })
    });
    this.setState({ justSaved: true });
  };

  
  render() {
    if (this.state.justSaved) return <div>Saved</div>;
    return (
      <JSONSchemaForm
        schema={this.state.schema}
        onSubmit={(event) => this.submit(event)}
      />
    );
  }
}

export default App;



